// src/hooks/useSafeZoneVerification.js
import { useState, useCallback } from 'react';

/**
 * Calculate distance between two GPS coordinates using Haversine formula
 * @returns Distance in meters
 */
const calculateDistance = (lat1, lon1, lat2, lon2) => {
    const R = 6371e3; // Earth's radius in meters
    const œÜ1 = (lat1 * Math.PI) / 180;
    const œÜ2 = (lat2 * Math.PI) / 180;
    const ŒîœÜ = ((lat2 - lat1) * Math.PI) / 180;
    const ŒîŒª = ((lon2 - lon1) * Math.PI) / 180;

    const a = Math.sin(ŒîœÜ / 2) * Math.sin(ŒîœÜ / 2) +
              Math.cos(œÜ1) * Math.cos(œÜ2) *
              Math.sin(ŒîŒª / 2) * Math.sin(ŒîŒª / 2);
    const c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a));

    return R * c; // Distance in meters
};

/**
 * Hook for managing safe zone verification during high-value exchanges
 * TODO: Migrate safe zone queries to Neon
 * Currently provides basic distance calculation without database queries
 */
export function useSafeZoneVerification(transactionId, userId) {
    const [currentPosition, setCurrentPosition] = useState(null);
    const [locationError, setLocationError] = useState(null);
    const [isInSafeZone, setIsInSafeZone] = useState(false);
    const [nearestSafeZone, setNearestSafeZone] = useState(null);
    const [safeZones, setSafeZones] = useState([]);
    const [otherPartyPosition, setOtherPartyPosition] = useState(null);
    const [proximityToOther, setProximityToOther] = useState(null);
    const [isWithinProximity, setIsWithinProximity] = useState(false);

    /**
     * Get current GPS position
     */
    const getCurrentPosition = useCallback(() => {
        if (!navigator.geolocation) {
            setLocationError('Geolocation not supported');
            return null;
        }

        navigator.geolocation.getCurrentPosition(
            (position) => {
                const pos = {
                    lat: position.coords.latitude,
                    lng: position.coords.longitude,
                    accuracy: position.coords.accuracy
                };
                setCurrentPosition(pos);
                return pos;
            },
            (error) => {
                setLocationError(error.message);
                return null;
            }
        );
    }, []);

    /**
     * Verify if user is in safe zone
     * TODO: Implement Neon query for safe_zones table
     */
    const verifySafeZone = useCallback(async () => {
        if (!currentPosition) return false;

        // TODO: Query safe_zones from Neon database
        console.warn('Safe zone verification not yet implemented with Neon');
        return false;
    }, [currentPosition]);

    /**
     * Verify proximity to other party
     */
    const verifyProximity = useCallback(() => {
        if (!currentPosition || !otherPartyPosition) return null;

        const distance = calculateDistance(
            currentPosition.lat,
            currentPosition.lng,
            otherPartyPosition.lat,
            otherPartyPosition.lng
        );

        setProximityToOther(distance);
        setIsWithinProximity(distance <= 100); // 100m threshold
        return distance;
    }, [currentPosition, otherPartyPosition]);

    return {
        currentPosition,
        locationError,
        isInSafeZone,
        nearestSafeZone,
        safeZones,
        otherPartyPosition,
        proximityToOther,
        isWithinProximity,
        getCurrentPosition,
        verifySafeZone,
        verifyProximity
    };
}
